<template>
	<view class="my-address-list">
		
		<view class="address-list">
			
			<view v-for="(path,index) in list" :key="index" @click="updatePath(index)">
				<view class="address-item" @click="goOrder(path)">
					<view class="item-main">
						<view class="main-name">{{path.name}}</view>
						<view>{{path.phone}}</view>
					</view>
					<view class="item-main">
						<view class="active" v-if="path.isDefault">默认</view>
						<view>{{path.city}} {{path.detail}}</view>
					</view>
				</view>
			</view>
			
		</view>
		
		<view class="add-address">
			<view class="add-btn" @click="addAddress">+新增地址</view>
		</view>
		
	</view>
</template>

<script>
	import {mapState} from 'vuex'
	export default {
		data() {
			return {
				isSelectPath:false,
			}
		},
		methods: {
			//添加地址
			addAddress(){
				uni.navigateTo({
					url:"/pages/zzh/add-address/add-address"
				})
			},
			//修改地址
			updatePath(index){
				if(!this.isSelectPath){
					let pathObj = JSON.stringify({
						index:index,
						item:this.list[index]
					})
					uni.navigateTo({
						url:"/pages/zzh/add-address/add-address?data="+pathObj+""
					})
				}
			},
			// 返回确认订单页面
			goOrder(path){
				// 从订单页过来才会执行
				if(this.isSelectPath){
					uni.$emit("selectPathItem",path)
					uni.navigateBack({
						delta:1
					})
				}
			}
		},
		computed:{
			...mapState({
				list:state=>state.path.list
			})
		},
		onLoad(e) {
			if(e.type==='selectPath'){
				this.isSelectPath = true
			}
		}
	}
</script>

<style>
.add-address{
	width: 100%;
	display: flex;
	justify-content: center;
}
.add-btn{
	border: 2rpx solid coral;
	background-color: coral;
	color: white;
	border-radius: 30rpx;
	padding: 10rpx 120rpx;
}
.address-list{
	padding-left: 20rpx;
	padding-bottom: 10rpx;
}
.address-item{
	padding: 10rpx 0;
	border: 2rpx solid gainsboro;
}
.item-main{
	display: flex;
}
.main-name{
	padding-right: 20rpx;
}
.active{
	padding: 2rpx 6rpx;
	background-color: lightblue;
	color: #FFFFFF;
	border-radius: 10rpx;
}

</style>
